.UtilityBar {
	-ms-flex: 0 0 50px;
	    flex: 0 0 50px;
	background-color: #FF6600;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	z-index: 1000;
	/*column-gap: 20px;  applies to flex only on FF currently,
		so for now stick to the .Ut > div margin stuff below */
}

.UtilityBar > * {
  font-family: 'Montserrat', 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 1.1em;
  line-height: 24px;
}

.UtilityBar > div {
	/* Spacing between flex items */
	margin-right: 10px;
	margin-left: 10px;
	margin-top: 5px;
}

.UtilityBar > div > button  {
	padding: 5px 15px;
	border-radius: 4px;
	background-color: white;
	-webkit-box-shadow: 0 1px 5px rgba(0,0,0,0.65);
	        box-shadow: 0 1px 5px rgba(0,0,0,0.65);
	border: none;
}

.UtilityBar > div > button:hover  {
	background-color: #e5e5e5;
}

.UtilityBar > div > button:active  {
	background-color: #bfbfbf;
}

.UtilityBar > div > button > span {
	padding: 5px 15px;
}

.UtilityBar input[type='checkbox'] {
	vertical-align: middle;
}

.UtilityBar label {
 	white-space: nowrap;
 	-webkit-user-select: none;
 	   -moz-user-select: none;
 	    -ms-user-select: none;
 	        user-select: none;
}

.UtilityBar label:active {
 	font-weight: 500;
}


