.ViewBar-container {
	background-color: #FF6600;
	position: relative;
}

.ViewBar-container .flip-move {
	overflow-x: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap:nowrap;
	    flex-wrap:nowrap;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: normal;
	    justify-content: normal;
	-webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    margin-left: 40px;
    margin-right:40px;
}

.bottom {
	bottom:0;
}



